.tile {
  background-color: rgba(50%, 50%, 50%, 0.1);
}


.safe {
  background-color: rgba(255, 255, 255, 0.5);
}
.hell {
  background-color: rgba(85%, 11%, 11%, 0.15);
}

@media only screen and (min-width: 600px) {
  .tile:hover{
    background-color: rgba(100%, 100%, 100%, 0.7);
  }
}



